* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: radial-gradient(#FEFFFF, #A7CECC); */
  
  background: url(../image/map.jpg) no-repeat center top fixed;
  background-size: cover;
  background-color: aliceblue;
  color: aliceblue;
}

#search-box {
  display: block;
  font: inherit;
  font-size: 24px;
  font-weight: 500;
  padding: 10px;
  max-width: 500px;
  margin: 300px auto 50px;
  border: none;
  border-radius: 10px;
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.05), 0 15px 40px rgba(0, 0, 0, 0.1);
}
#search-box:focus {
  outline: none;
}
h2 {
  margin: 0;
}
.country-link {
  text-decoration: none;
  color: inherit;
  display: block;
  
}
.country-link:hover {
  transform: scale(1.03);
}
.country-list {
  padding: 0 30px;
  list-style: none;
  font-size: 20px;
  
}
.country-list li {
  padding-bottom: 10px;
  border-width: 1px;
    border-color: grey;
    box-shadow: 0px 14px 5px -5px rgba(10, 78, 131, 0.45);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    width: 370px;
    margin: 0 auto;
}
.country-card {
  font-size: 20px;
  padding: 30px;
 
}
.country-info {
 display: flex;
 justify-content: center;
}
.country-title-box {
  display: flex;
  align-items: center;
}
.country-flag {
  margin-right: 5px;
}
p {
  font-weight: 700;
}
span {
  font-weight: 400;
}

.languages-item:not(:last-child):after {
content: ',';
}
.country-title {
  font-weight: 400;
}